.tab-btn{
    background-color: transparent;
    border: 1px solid #e1e1e1;
    transition: all .3s ease-out;
    color: #000000;
    outline-color: transparent;
    i{
        display: inline-block;
        color: currentColor;
        font-size: 14px;
    }
    &[data-content='icon']{
       display: flex;
       align-items: center;
       justify-content: center;
       width: 37px;
       height: 37px;
       border-radius: 10px;
    }
    &:not(:last-child){
        margin-right: 10px;
    }
    &:hover, &.active{
        border-color: #e08b44;
        color: #e08b44;
        transition: all .3s ease-out;
    }
    &:focus{
        outline: none;
    }
    &s{
        display: flex;
    }
}
.tab-container{
    display: none;
}
@media(max-width: 575px){
    .tab-btns[data-hide="yes"]{
        display: none!important;
    }
    .tab-containers[data-hide="yes"]{
        .tab-container:not(:last-of-type){
            .pagination{
                display: none;
            }
        }
        .tab-container{
            display: block!important;
        }
    }
}